@tailwind base;
@tailwind components;
@tailwind utilities;

/* General body styling for dark theme */
body {
  font-family: sans-serif;
  margin: 0;
  background-color: #121212;
  color: #e0e0e0;
}

.container {
  max-width: 900px;
  margin: auto;
  padding: 1rem;
}

h1 {
  text-align: center;
  font-size: 2rem;
  margin-bottom: 1rem;
  color: #ffffff;
}

form,
.filter-panel {
  background: #1e1e1e;
  padding: 1rem;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(255, 255, 255, 0.05);
  margin-bottom: 1rem;
}

input,
select,
button {
  width: auto;
  min-width: 180px;
  flex: 1;
  padding: 0.5rem;
  margin-top: 0.2rem;
  border-radius: 6px;
  border: 1px solid #444;
  font-size: 1rem;
  background-color: #2c2c2c;
  color: #e0e0e0;
  transition: background-color 0.2s ease, border-color 0.2s ease;
}

input,
select {
  background-color: #2c2c2c;
  border-color: #555;
  color: #ddd;
}

input:focus,
select:focus,
button:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 5px 1px #3b82f6;
}

button {
  background-color: #3b82f6;
  color: white;
  border: none;
  cursor: pointer;
  transition: background 0.2s ease;
}

button:hover {
  background-color: #2563eb;
}

.card-gallery {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 1rem;
  margin-bottom: 2rem;
}

.card {
  background-color: #1e1e1e;
  border-radius: 12px;
  padding: 12px;
  width: fit-content;
  max-width: 240px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}

.card img {
  width: 100%;
  height: auto;
  border-radius: 12px;
  object-fit: contain;
  display: block;
  max-width: 100%;
}

.card h3 {
  margin: 0.5rem 0;
  color: #fff;
  font-family: "Fira Code", monospace;
}

.card p {
  font-size: 0.9rem;
  margin: 0.2rem 0;
  color: #ccc;
}

.card .tags {
  font-size: 0.85rem;
  color: #aaa;
}

.card .actions {
  margin-top: 0.5rem;
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.card.unowned {
  border: 1px dashed #666;
  opacity: 0.85;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.modal img {
  max-width: 90vw;
  max-height: 90vh;
  border-radius: 8px;
  display: block;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.85);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.modal-content {
  position: relative;
  background: #1e1e1e;
  padding: 1rem;
  border-radius: 10px;
  max-width: 90vw;
  max-height: 90vh;
  overflow: auto;
  box-shadow: 0 0 15px rgba(255, 255, 255, 0.1);
}

.modal-content img {
  max-width: 100%;
  max-height: 80vh;
  display: block;
  margin: 0 auto;
  border-radius: 8px;
}

.close-btn {
  margin-top: 0.8rem;
  background: #ef4444;
  color: white;
  border: none;
  padding: 0.4rem 1rem;
  font-size: 1rem;
  border-radius: 6px;
  cursor: pointer;
  display: block;
  margin-left: auto;
}

.close-btn:hover {
  background: #dc2626;
}

.card-gallery + h2 {
  margin-top: 2rem;
}
/* Rarity-based card colors */
.card-common {
  background-color: #dcd6dd;
}
.card-rare {
  background-color: #382f3b;
}
.card-epic {
  background-color: #2b2f4e;
}
.card-legendary {
  background-color: #403418;
}
.card-mystic {
  background-color: #3e3053;
}
.card-celestial {
  background-color: #1f2e40;
}

/* Fix buttons overflow inside card */
.card .actions button {
  flex: 1;
  min-width: 0;
  padding: 0.5rem;
  font-size: 0.9rem;
}

/* Keep buttons side-by-side without overflow */
.card .actions {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  width: 100%;
}

/* Ensure tags have proper margin */
.card .tags {
  margin-top: 0.4rem;
  color: #aaa;
  font-size: 0.85rem;
  text-align: center;
}

/* Modal close button alignment fix */
.modal-content .close-btn {
  margin: 1rem auto 0;
  display: block;
  text-align: center;
}
